import React from "react";
import Nav from "../../components/Navbar/Nav";
import { useState } from "react";
import toast, { Toaster } from "react-hot-toast";
import axios from "axios";
import NavLink from "react-bootstrap/esm/NavLink";
import { useNavigate } from "react-router-dom";
const Fpassword = () => {
  const [email, Setemail] = useState("");
  const [newpassword, Setnewpassword] = useState("");
  const [answer, Setanswer] = useState("");
  const Navigate = useNavigate();

  const HandleSubmit = async (e) => {
    try {
      e.preventDefault();

      let res = await axios.post("/api/auth/forgetpassword", {
        email,
        newpassword,
        answer,
      });

      if (res && res.data.success) {
        toast.success(res.data.message);

        Navigate("/login");
      } else {
        toast.error(res && res.data.message);
      }
    } catch (error) {
      toast.error("something went wrong!");
    }
  };

  return (
    <div>
      <Nav />
      <Toaster />
      <div className="center">
        <h4>Reset Password</h4>
        <form onSubmit={HandleSubmit}>
          <div className="txt_field">
            <input
              type="text"
              name="email"
              value={email}
              onChange={(e) => Setemail(e.target.value)}
              required
            />
            <span></span>
            <label>email</label>
          </div>
          <div className="txt_field">
            <input
              type="text"
              name="answer"
              value={answer}
              onChange={(e) => Setanswer(e.target.value)}
              required
            />
            <span></span>
            <label>
              what is your favourite sport name ?<sup>*</sup>
            </label>
          </div>
          <div className="txt_field">
            <input
              type="text"
              name=" newpassword"
              value={newpassword}
              onChange={(e) => Setnewpassword(e.target.value)}
              required
            />
            <span></span>
            <label> new password</label>
          </div>

          <input type="submit" value="Resetpassword" />
        </form>
        <div className="signup_link">
          <NavLink onClick={() => Navigate("/signup")} className="nav-link">
            Signup
          </NavLink>
        </div>
      </div>
    </div>
  );
};

export default Fpassword;
